module.exports = ".vm--modal {\n  max-height: 100% !important;\n  max-width: 100% !important;\n}\n\n.imjoy-inline-window {\n  width: 100%;\n  height: 600px;\n}\n\n.imjoy-progress-border > div {\n  background-color: #448aff;\n  height: 3px;\n}\n\n.imjoy-progress-border {\n  top: 0;\n  left: 0;\n  position: absolute;\n  width: 100%;\n  height: 3px;\n}\n\n.imjoy-noselect {\n  -webkit-touch-callout: none;\n  -webkit-user-select: none;\n  -khtml-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n\nbody {\n  margin: 0px;\n}\n.imjoy-dialog-control {\n  height: 23px;\n  border: 0px;\n  font-size: 1rem;\n  position: absolute;\n  color: white;\n  top: 1px;\n}\n\n.imjoy-dialog-control:focus {\n  outline: none;\n}\n\n.imjoy-loader {\n  position: fixed;\n  top: 40%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  transform: -webkit-translate(-50%, -50%);\n  transform: -moz-translate(-50%, -50%);\n  transform: -ms-translate(-50%, -50%);\n  border: 10px solid #f3f3f3;\n  /* Light grey */\n  border-top: 10px solid #448aff;\n  /* Blue */\n  border-radius: 50%;\n  width: 40px;\n  height: 40px;\n  animation: spin 2s linear infinite;\n}\n\n@keyframes spin {\n  0% {\n    transform: rotate(0deg);\n  }\n\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n/* The snackbar - position it at the bottom and in the middle of the screen */\n.imjoy-snackbar {\n  font-family: Arial, Helvetica, sans-serif;\n  visibility: hidden;\n  /* Hidden by default. Visible on click */\n  min-width: 250px;\n  /* Set a default minimum width */\n  background-color: #333333c2;\n  /* Black background color */\n  color: #fff;\n  /* White text color */\n  text-align: center;\n  /* Centered text */\n  border-radius: 5px;\n  /* Rounded borders */\n  padding: 16px;\n  /* Padding */\n  position: fixed;\n  /* Sit on top of the screen */\n  z-index: 9999;\n  /* Add a z-index if needed */\n  left: 50%;\n  /* Center the snackbar */\n  transform: translate(-50%, 0);\n  bottom: 20px;\n  /* 20px from the bottom */\n}\n\n.imjoy-window-title-bar {\n  cursor: move;\n  background-color: #448aff;\n  color: white;\n  text-align: center;\n  height: 24px;\n}\n\n.imjoy-window-title {\n  text-align: center;\n  font-family: Arial, Helvetica, sans-serif;\n  font-size: 16px;\n  line-height: 20px;\n  margin: 0px;\n}\n\n/* Show the snackbar when clicking on a button (class added with JavaScript) */\n.imjoy-snackbar.show-snackbar {\n  visibility: visible;\n  /* Show the snackbar */\n  /* Add animation: Take 0.5 seconds to fade in and out the snackbar.\n  However, delay the fade out process for 2.5 seconds */\n  -webkit-animation: imjoy-fadein 0.5s;\n  animation: imjoy-fadein 0.5s;\n}\n\n/* Animations to fade the snackbar in and out */\n@-webkit-keyframes imjoy-fadein {\n  from {\n    bottom: 0;\n    opacity: 0;\n  }\n\n  to {\n    bottom: 30px;\n    opacity: 1;\n  }\n}\n\n@keyframes imjoy-fadein {\n  from {\n    bottom: 0;\n    opacity: 0;\n  }\n\n  to {\n    bottom: 30px;\n    opacity: 1;\n  }\n}\n\n.dropdown-btn {\n  padding: 4px;\n  font-size: 16px;\n  border: none;\n  cursor: pointer;\n}\n\n.dropdown {\n  position: relative;\n  display: inline-block;\n}\n\n.dropdown-content {\n  display: none;\n  position: absolute;\n  right: 0;\n  background-color: #f9f9f9;\n  min-width: 200px;\n  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);\n  z-index: 1;\n}\n\n.dropdown-content a {\n  font-family: Arial, Helvetica, sans-serif;\n  color: black;\n  padding: 6px 10px;\n  text-decoration: none;\n  display: block;\n}\n\n.dropdown-content a:hover {\n  background-color: #f1f1f1;\n}\n\n.dropdown:hover .dropdown-content {\n  display: block;\n}\n\n.dropdown:hover .dropbtn {\n  background-color: #3e8e41;\n}\n\n.imjoy-window-container {\n  width: 100%;\n  height: 100%;\n}\n\n.imjoy-windows {\n  position: relative;\n  min-height: 100px;\n  height: calc(100% - 30px);\n}\n"